// Sizes
$screen-tablet: 768px;
$screen-mobile: 425px;

// Desktop
@import "app/normalize";
@import "app/prototype";
@import "app/common";
@import "app/spoiler";
@import "app/header";
@import "app/sidebar";
@import "app/home";
@import "app/breadcrumbs";
@import "app/fancybox";
@import "app/category";
@import "app/article";
@import "app/pagination";

// Tablet
@media screen and(max-width: $screen-tablet) {
  @import "app/tablet/header";
  @import "app/tablet/sidebar";
  @import "app/tablet/prototype";
  @import "app/tablet/article";
}

// Mobile
@media all and (max-width: $screen-mobile) {
  @import "app/mobile/prototype";
}